import React, { Component } from 'react'
import { Row, Col, Input, Button, Modal } from 'antd';
import './indexCont.css';
import { Records } from '../Table/instable';
import { HisSearch } from '../Table/search';
import PropTypes from "prop-types";
import Cookies from 'universal-cookie';

const cookies = new Cookies();
var ins;
const name = cookies.get('userName') == 'admin'?'保险公司': cookies.get('userName')== '20172122'?'张全': '莫四七';
function error() {
	Modal.error({
	  title: 'This is an error message',
	  content: '解密失败，可能密钥错误或属性不符',
	});
  }
  function warning() {
	Modal.warning({
	  title: 'This is a warning message',
	  content: '请输入密钥',
	});
  }

class Dec extends React.Component {
	constructor(props) {
		super(props);
	}
	state = { visible: false };
  
	showModal = () => {
	  this.setState({
		visible: true,
	  });
	};
  
	handleOk = e => {
	  console.log(e);
	  this.setState({
		visible: false,
	  });
	};
  
	handleCancel = e => {
	  console.log(e);
	  this.setState({
		visible: false,
	  });
	};

	getResult = () => {
		console.log(this.props.pkey);
		if(this.props.pkey!=''){
			if(this.props.record.prikey==this.props.pkey && this.props.record.name==name)
				this.showModal()
			else{
				error()
			}
		}
		else{
			warning()
		}
	}

	render() {
	  return (
		<div>
			{this.props.Bid!=null?
		  <Button type="primary" onClick={this.getResult }  >
			查询
		  </Button>:
		  <Button type="primary" onClick={this.getResult }  disabled>
		  查询
		</Button>

			}
		  <Modal
			title="交易数据（解密）"
			visible={this.state.visible}
			onOk={this.handleOk}
			onCancel={this.handleCancel}
		  >
			<p>区块号：{this.props.Bid}</p>
			<p>理赔单号：<span>{this.props.record.id}</span></p>
			<p>发送人: {this.props.record.name}</p>
			<p>车牌号: {this.props.record.pleteData}</p>
			<p>交易金额: {this.props.record.money}</p>
			<p>保险：{this.props.record.ins}</p>
			<p>交易时间：{this.props.record.time}</p>
			<p>Status：<span style={this.props.record.state=="申请中"?{color: "green"}:this.props.record.state=="驳回申请"?{color: "red"}:{color: "blue"}}>{this.props.record.state}</span></p>
		  </Modal>
		</div>
	  );
	}
  }

export class IndexCont extends Component {
	constructor(props,context){
		super(props,context);   //调用父类构造函数
	}
	state = {
		insId : '',
		record : {},
		value: '',
	  }
	
	
// 子组件声明自己需要使用 context
static contextTypes = {
	blockId: PropTypes.string,
	transaction: PropTypes.string,
	id: PropTypes.string,
	web3: PropTypes.object,
	accounts: PropTypes.object,
	status: PropTypes.string,
	contract: PropTypes.object,
	last: PropTypes.string,
    now: PropTypes.string,
	callback: PropTypes.func
  }
	
	//父组件提供一个回调函数，获取HisSearch组件中的数据
    getSearch(newId) {
		this.setState({     //修改状态值
		  insId: newId     
		})
	  }
	onChange = ({ target: { value } }) => {
	this.setState({ value });
	console.log(this.state.value);
	};

    render() {
        return (
            <div className="container" >
				
				<div className="left img">
					<HisSearch callback={newId=>this.getSearch(newId)}/>
					<Records value={this.state.insId} callback={(newRecord)=>{this.setState({record: newRecord})}}/>
				</div>
			
				<div className="right query">
					<Row>
						<Col span={18} style={{color: "#0071BC", fontSize: "28px", paddingTop: "20px"}}>当前区块链高度<br/><span style={{color: "white", fontSize: "20px"}}>{this.context.blockId}</span></Col>
						<Col span={6}></Col>
					</Row>
					<Row>
						<Col className="hash" >最新块哈希</Col>
					</Row>
					<Row>
						<Col className="bga small">{this.context.now}</Col>
					</Row>
					<Row>
						<Col className="hash ">上一块哈希</Col>
					</Row>
					<Row>
						<Col className="bga small ">{this.context.last}</Col>
					</Row>
					<Row>
						<Col style={{background: "#0071BC", height: "6px", marginTop:"30px"}}></Col>
					</Row>
					<Row>
						<Col className="hash ">当前理赔单号 {this.state.record.id}</Col>
					</Row>
					<Row>
						<Col className="bga small "><span style={{color: "red", fontSize: 18}}>Sender Address:</span><br/>{this.state.record.account}</Col>
					</Row>
					<Row>
						<Col style={{margin:"30px"}}><Input placeholder="密钥" value={this.state.value} onChange={this.onChange}/></Col>
					</Row>
					<Row>
						<Col span={10}></Col>
						<Col span={5}><Dec pkey={this.state.value} Bid={this.state.record.blockId} record={this.state.record}/></Col>
					</Row>
				</div>
            </div>
        )
    
    }
}