import React, { Component } from 'react';
import { Row, Col, Button, Input, Tabs, Radio, message, Modal, Popconfirm } from 'antd';
import '../Owner/owner';
import './insCompany.css';
import { InsList } from '../Table/list';
import { FixInfo } from "../Table/equiptable";
import { HisSearch } from '../Table/search';

const Search = Input.Search;
const TabPane = Tabs.TabPane;
const RadioGroup = Radio.Group;
const { TextArea } = Input;
const liablity = `当发生了交通事故以后，车主必须要在第一时间内亮起车辆的危险警告灯，并在车后面设置危险警告标识牌。这样做的好处就是防止有后面的车进行追尾的碰撞，从而保障车内的人员以及车辆自身的安全。`;
const result = `除了设立了警示标识以后，还要对事故的现场进行拍照取证，这是向保险公司进行理论的事故证据，在移动车辆之前进行拍照的方法主要是为了避免车主不认问题出现。`;

function callback(key) {
    console.log(key);
}
//单选框
class InsRadio extends Component {
    state = {
      value: 1,
    };
  
    onChange = e => {
      console.log('radio checked', e.target.value);
      this.setState({
        value: e.target.value,
      });
    };
  
    render() {
      return (
        <RadioGroup onChange={this.onChange} value={this.state.value}>
          <Radio value={1}>通过</Radio>
          <Radio value={2}>驳回</Radio>
        </RadioGroup>
      );
    }
}
//确认框
function confirm(e) {
    console.log(e);
    message.success('确认成功');
  }
  
function cancel(e) {
    console.log(e);
    message.error('取消');
  }

export class InsCompany extends Component{
    constructor(props) {
        super(props);
    }
    state = {
        insId : ''
    }
      //获取HisSearch组件中的数据
      getSearch(newId) {
        this.setState({     //修改状态值
          insId: newId     
        })
      }
    render() {
        return(
            <div className="container">
                <div className="left lfpart" style={{paddingLeft: '5%', marginTop: '2%'}}>
                    <Row>
                        <Col span={14} ><h2 className="left"><b>保单列表</b></h2></Col>
                        <Col span={10}><h2><b>查询</b><HisSearch callback={newId=>this.getSearch(newId)}/></h2></Col>
                    </Row>
                    <div style={{width: '100%'}}>
                        <InsList value={this.state.insId}/>
                    </div>
                </div>
                <div className="right rtpart" >
                    <div className="img">
                        <img src="img/car.png" alt="" />
                    </div>
                    <div>
                        <Tabs onChange={callback} type="card" tabPosition="left">
                            <TabPane tab="受损设备维修费用" key="1">
                                <FixInfo />
                            </TabPane>  
                            <TabPane tab="事故与保单详情" key="2">
                                <h2><b>事故责任</b></h2>
                                <TextArea rows={8} defaultValue={liablity}></TextArea>
                                <TextArea rows={8} defaultValue={result}></TextArea>
                                <h2><b>保单详情</b></h2>
                                <a href="#">www.baidu.com</a>
                            </TabPane>
                            <TabPane tab="申请处理" key="3">
                                <InsRadio />
                                <h2 className="left"><b>理赔金额</b></h2>
                                <TextArea rows={1} defaultValue='壹万元整'></TextArea>
                                <h2 className="left"><b>理赔明细</b></h2>
                                <TextArea rows={8} defaultValue={result}></TextArea>
                                <Popconfirm
                                    title="是否确认申请?"
                                    onConfirm={confirm}
                                    onCancel={cancel}
                                    okText="Yes"
                                    cancelText="No"
                                >
                                    <Button type="primary" className="right" style={{marginRight: "5%", marginTop: "2%"}}>确认</Button>
                                </Popconfirm>
                            </TabPane>
                        </Tabs>
                    </div>
                </div>
            </div>
        );
    }
}


